*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}


:root {
  font-size: 62.5%;
}

body 
{   visibility: hidden;
	 min-width: 200px;
	   overflow-x: hidden;
    

}

/*   main container of the body   */
.container {
  display: grid;
  grid-template-columns:
    [container-start] 1fr [center-start] repeat(
      10,
      [col-start] minmax(min-content, 11rem) [col-end]
    )
    [center-end] 1fr [container-end];
  grid-template-rows: 50vh 50vh auto;
}

/*   mstyling for the navigation  */

.headerwrapper{
  grid-column: container-start / container-end;
  grid-row:1/1;
z-index: 2;
display: grid;
grid-template-columns: repeat(3,1fr);
}

.headw1{
background:black;

}

.headsss{

    grid-column: container-start / container-end;
    grid-row:1/1;
    z-index: 3;
    color: white;
    

}

.headsss h3 {

  color: black;
  font-size: 2rem;
  letter-spacing: 0.2rem;
  padding-top: 1vh;
  padding-left: 10vw;
  font-family: 'Manrope', sans-serif;
}

.headsss .heads1{

  padding-top: 15vh;
  padding-left: 10vw;
  font-size: 4rem;
  font-family: 'Manrope', sans-serif;
}
.headsss .heads2{

  padding-top: 1vh;
  padding-left: 10vw;
  font-family: 'Manrope', sans-serif;
}

.headsss h2{

  padding-top: 5vh;
  padding-left: 10vw;
  color: black;
  font-size: 1.5rem;
 }

.header {

	grid-column: container-start / container-end;
    grid-row:1/1;
 background: #222;
z-index: 5;
  background-color: var(--secondary-color);
  height:calc(3.5vw + 3.5vh);
  z-index: 1;
  
}

.herowrapper{

grid-column: container-start / container-end;
grid-row:2/2;

z-index: 2;
display: grid;
grid-template-columns: repeat(3,1fr);
}

.hw1{
  background: black;
}

.hero{
grid-column: container-start / container-end;
grid-row:2/2;
  
  z-index: 1;
  display: grid;
grid-template-columns: repeat(3,1fr);
}

.her1{


   position: relative;
}

.her1 .hertext {
  position: absolute;
  top:20vh;
  left: 10vw;
  z-index:2;
  color: white;
  margin:auto;
  font-size: 3rem;
}
.her1 .imgholder{

  height: 50vh;
  width:100%;
  blur: 0.15;
  overflow:hidden;
}

.her1 .imgholder img:hover{filter: brightness(40%)}

.her1 .imgholder img {

  height: 50vh;
  width: 100%;
  object-fit: cover;
  filter: brightness(60%)
}


@media screen and (max-width: 768px)  {

.container{

  grid-template-rows: 50vh 50vh 50vh 50vh auto;
}
.hero{
  grid-row:2/4;
  display: grid;
grid-template-columns: repeat(1,1fr);

}


.herowrapper{
  grid-row:2/4;
   display: grid;
grid-template-columns: repeat(1,1fr);

}
.her1,.hw1{
  height: 50vh;
}

.headw12,.headw13{
  display: none;
}


.headwrapper{
  grid-row:1/1;
   display: grid;
  grid-template-columns: repeat(1,1fr);
}

.headw1{
  width:100vw;
}

.headsss h3 {

  color: black;
  font-size: 2rem;
  letter-spacing: 0.2rem;
  padding-top: 1vh;
  padding-left: 10vw;
}

.headsss .heads1{

  padding-top: 10vh;
  padding-left: 10vw;
  font-size: 3rem;
}
.headsss .heads2{

  padding-top: 1vh;
  padding-left: 10vw;
}
.her1 .hertext {
  position: absolute;
  top:20vh;
  left: 35vw;
  z-index:2;
  color: white;
  margin:auto;
  font-size: 3rem;
}
}


@media screen and (max-width: 480px)  {

.headsss h3 {

  color: black;
  font-size: 2rem;
  letter-spacing: 0.2rem;
  padding-top: 1vh;
  padding-left: 5vw;
}

.headsss .heads1{

  padding-top: 10vh;
  padding-left: 5vw;
  font-size: 2.5rem;
}
.headsss .heads2{

  padding-top: 1vh;
  padding-left: 5vw;
}
  }


@media screen and (max-width: 380px)  {

.headsss h3 {

  color: black;
  font-size: 2rem;
  letter-spacing: 0.2rem;
  padding-top: 1vh;
  padding-left: 5vw;
}

.headsss .heads1{

  padding-top: 10vh;
  padding-left: 5vw;
  font-size: 2rem;
}
.headsss .heads2{

  padding-top: 1vh;
  padding-left: 5vw;
  font-size: 1.5rem;
}
  }