.info{
	


	grid-column: container-start / container-end;
	display: grid;
	grid-template-columns: 60% 40%;
   background:#fbf5e9;
   

	
}

.info .info_text{


	display:flex;
	flex-direction: column;
	align-items: center;
	text-align: center;
	margin: auto;

}

.info .info_text h1 {
	font-size:5rem;
	padding-top: 5rem;
	
}

.info .info_text h2:first-child {
	
	padding-top: 5rem;
	
}

.info .info_text h2 {
	font-size:2.5rem;
	padding-top: 4rem;
	font-family: 'Fondamento', cursive;
	letter-spacing: 0.2rem;
}

.info .info_graphic{

	position: relative;
	  background:#fbf5e9;
	 
}
.info .ig1{

    
    
position: absolute;
	
	height:50rem;
	width: 45rem;
	padding-left: 10rem;
		padding-top: 5rem;

}

.info .ig2{
position: absolute;
    top:3rem;
	left:8rem;
	height:35rem;
	width: 35rem;
	  background:#fbf5e9;
}



@media screen and (max-width: 950px)  {

.info .ig1{

    
    
position: absolute;
	
	height:45rem;
	width: 35rem;
	padding-left: 5rem;
	padding-top: 15rem;

}

.info .ig2{
position: absolute;
    top:13rem;
	left:6rem;
	height:25rem;
	width: 25rem;
	
}



	}




